import Divider from "@material-ui/core/Divider";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Checkbox from "@material-ui/core/Checkbox";
import React, { useState, useEffect } from "react";
import data from "../data";

import { useStyles } from "./styles";

const DrawerComponent = () => {
  const classes = useStyles();
  const [activeFilter, setActiveFilter] = useState([]);
  const [allCategories, setAllCategories] = useState(data.map((a) => a.name));
  useEffect(() => {
    setActiveFilter(
      data
        .filter((e) => e.description.includes("Emporio Armani"))
        .map((e) => e.name)
    );
  }, []);
  // const handleChange = (text) => setActiveFilter((prev) => [...prev, { text }]);

  // const handleChange = (text) => setActiveFilter((prev) => ([...prev, {text}]));

  const handleChange = (text) => {
    if (activeFilter.some((country) => country === text)) {
      const updatedList = activeFilter.filter((country) => country !== text);
      return setActiveFilter(updatedList);
    }
    return setActiveFilter((prev) => [...prev, text]);
  };

  console.log("activeFilter: ", activeFilter);

  return (
    <div className={classes.root}>
      <div className={classes.toolbar} />
      <Divider />
      <List className={classes.list}>
        {allCategories.sort().map((text, index) => (
          <ListItem className={classes.itemList} button key={text}>
            <Checkbox
              onChange={() => handleChange(text)}
              checked={activeFilter.includes(text)}
            />
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List>
    </div>
  );
};

export default DrawerComponent;
